
.button {
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    margin-left: 0.5rem;
    color: var(--red-theme-color);
    border-radius: 5px;
    background-color: var(--grey-theme-color);
    border: 1px solid var(--grey-theme-color);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
    transition: 0.7s;
    font-weight: bold;
    cursor: pointer;
}

.button:hover {
    background-color: var(--red-theme-color);
    color: var(--grey-theme-color);
    border-color: var(--grey-theme-color);
}

.button:focus {
    background-color: var(--red-theme-color);
    color: var(--grey-theme-color);
    border-color: var(--red-theme-color);
}